.merge {
  &_wrapper {
    max-width: 100%;
    background-color: #000000; } }

.merge {
  &_content {
    display: flex;
    min-height: 80vh;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    max-width: 1680px;
    margin: 0 auto;
    color: #FFF;
    font-size: 4vh;
    @media screen and (orientation: portrait) {
      font-size: 4vw; } }
  &__text {
    width: 55%;
    margin: 0 2.5%;
    font-weight: 300;
    color: $col_l;
    @include r(639) {
      width: 100%; } }

  &__img {
    max-width: 40%;
    @include r(639) {
      max-width: 100%; } } }
