 }  // background-position: center

.hero_wrapper {
  height: 100vh;
  position: relative;
  font-size: 9.6rem;
  // &:before
  //   display: block
  //   content: ""
  //   position: absolute
  //   top: 0
  //   bottom: 0
  //   left: 0
  //   right: 0
  //   background-color: rgba(92,106,113,.25)
  @include r(1500) {
    font-size: 9rem; }
  @include r(1400) {
    font-size: 8.5rem; }
  @include r(1300) {
    font-size: 8rem; }
  @include r(1200) {
    font-size: 7.5rem; }
  @include r(1100) {
    font-size: 7rem; }
  @include r(1000) {
    font-size: 6.5rem; }
  @include r(900) {
    font-size: 5.5rem; }
  @include r(800) {
    font-size: 4.5rem; }
  @include r(700) {
    font-size: 4rem; }
  @include r(600) {
    font-size: 3.5rem; }
  @include r(500) {
    font-size: 2.5rem; } }
.slogan {
  max-width: 12.5em;
  position: absolute;
  bottom: 24vh;
  font-weight: 300;
  font-size: 1em;
  color: $col_l;
  @media screen and (orientation: landscape) {
    font-size: 0.6em; } }

#background {
  display:none {}
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%); }

#video_overlay {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -80;
  top: 0%;
  left: 0%;
  background: rgba(0, 0, 0, .3); }

