/* глобальные параметры */
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: 'Montserrat', Arial, sans-serif;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.clearfix:after {
  display: block;
  content: "";
  clear: both;
}

.wrap {
  max-width: 1680px;
  margin: 0 auto;
  overflow: hidden;
}

.wrap_content {
  max-width: 1348px;
  margin: 0 auto;
  padding: 0 20px;
}

.wrap__video iframe {
  overflow: hidden;
  height: 100%;
  width: 100%;
  max-width: 720px;
  min-height: 576px;
  margin: 0 auto;
}

