.servises {
  background: $col_bg_d;
  font-size: 3.4rem;
  &__wrap {
    height: 75vh;
    position: relative; }
  &__item {
    &_top {
      position: absolute;
      border: 1px solid rgba($col_l, 0.5);
      width: 33%;
      height: 25vh;
      background-color: $col_bg_d;
      top: calc(50% - 25vh);
      @include r(812) {
        height: initial; }
      @include hover {
        height: 35vh;
        top: calc(50% - 35vh);
        background-color: $col_g;
        border: none;
        & .servises__description {
          display: block; }
        @include r(812) {
          height: initial;
          top: initial; } }
      &:nth-child(1) {
        left: 0.5%;
        @include r(812) {
          left: initial;
          top: initial; } }
      &:nth-child(2) {
        left: 33.5%;
        @include r(812) {
          left: initial;
          top: initial; } }
      &:nth-child(3) {
        left: 66.5%;
        @include r(812) {
          left: initial;
          top: initial; } }


      @include r(812) {
        position: relative;
        width: 100%;
        height: initial; } }


    &_bottom {
      position: absolute;
      border: 1px solid rgba($col_l, 0.5);
      width: 33%;
      height: 25vh;
      background-color: $col_bg_d;
      bottom: calc(50% - 25vh);
      @include hover {
        height: 35vh;
        bottom: calc(50% - 35vh);
        background-color: $col_g;
        border: none;
        & .servises__description {
          display: block; }
        @include r(812) {
          height: initial;
          bottom: initial; } }
      &:nth-child(4) {
        left: 0.5%;
        @include r(812) {
          left: initial;
          bottom: initial; } }
      &:nth-child(5) {
        left: 33.5%;
        @include r(812) {
          left: initial;
          bottom: initial; } }
      &:nth-child(6) {
        left: 66.5%;
        @include r(812) {
          left: initial;
          bottom: initial; } }
      @include r(812) {
        position: relative;
        width: 100%;
        height: initial; } } }


  &__title {
    font-weight: 300;
    font-size: 4vh;
    margin-top: 3vh;
    margin-left: 3vh;
    line-height: 2;
    @include r(812) {
      margin-top: 0; }
    color: $col_l; }
  &__description {
    margin-top: 3vh;
    margin-left: 3vh;
    margin-right: 3vh;
    font-size: 1.4vh;
    color: $col_l;
    display: none;
    @include r(812) {
      margin-top: 0.5vh; }
    @include r(1023) {
      font-size: calc(6px + 1.4vh); }
    & li {
      margin-bottom: 0.7em;
      padding-bottom: 0.5em;
      @include r(1023) {
        margin-bottom: initial; } } } }



.servises__active {
  height: 35vh;
  top: calc(50% - 35vh);
  background-color: $col_g;
  border: none;
  & .servises__description {
    display: block; }
  @include r(812) {
    height: initial;
    top: initial; } }





